'use client'

import { Button } from '@/components/ui/button'
import { cn } from '@/lib/utils'

const SECTIONS = [
  { key: 'you' as const, label: 'Você hoje', icon: IconUser },
  { key: 'aposentadoria' as const, label: 'Aposentadoria', icon: IconPension },
  { key: 'microPlans' as const, label: 'Micro planos', icon: IconLayers },
  { key: 'events' as const, label: 'Eventos', icon: IconCalendar },
  { key: 'configuracoes' as const, label: 'Configurações', icon: IconSliders },
] as const

function IconUser({ className }: { className?: string }) {
  return (
    <svg className={className} width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
      <path d="M19 21v-2a4 4 0 0 0-4-4H9a4 4 0 0 0-4 4v2" />
      <circle cx="12" cy="7" r="4" />
    </svg>
  )
}

function IconSliders({ className }: { className?: string }) {
  return (
    <svg className={className} width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
      <line x1="4" y1="21" x2="4" y2="14" />
      <line x1="4" y1="10" x2="4" y2="3" />
      <line x1="12" y1="21" x2="12" y2="12" />
      <line x1="12" y1="8" x2="12" y2="3" />
      <line x1="20" y1="21" x2="20" y2="16" />
      <line x1="20" y1="12" x2="20" y2="3" />
      <line x1="1" y1="14" x2="7" y2="14" />
      <line x1="9" y1="8" x2="15" y2="8" />
      <line x1="17" y1="16" x2="23" y2="16" />
    </svg>
  )
}

function IconLayers({ className }: { className?: string }) {
  return (
    <svg className={className} width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
      <polygon points="12 2 2 7 12 12 22 7 12 2" />
      <polyline points="2 17 12 22 22 17" />
    </svg>
  )
}

function IconCalendar({ className }: { className?: string }) {
  return (
    <svg className={className} width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
      <rect x="3" y="4" width="18" height="18" rx="2" ry="2" />
      <line x1="16" y1="2" x2="16" y2="6" />
      <line x1="8" y1="2" x2="8" y2="6" />
      <line x1="3" y1="10" x2="21" y2="10" />
    </svg>
  )
}

function IconPension({ className }: { className?: string }) {
  return (
    <svg className={className} width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
      <circle cx="12" cy="12" r="5" />
      <line x1="12" y1="1" x2="12" y2="3" />
      <line x1="12" y1="21" x2="12" y2="23" />
      <line x1="4.22" y1="4.22" x2="5.64" y2="5.64" />
      <line x1="18.36" y1="18.36" x2="19.78" y2="19.78" />
      <line x1="1" y1="12" x2="3" y2="12" />
      <line x1="21" y1="12" x2="23" y2="12" />
      <line x1="4.22" y1="19.78" x2="5.64" y2="18.36" />
      <line x1="18.36" y1="5.64" x2="19.78" y2="4.22" />
    </svg>
  )
}

export type EditSectionKey = 'you' | 'aposentadoria' | 'microPlans' | 'events' | 'configuracoes'

interface EditSidebarProps {
  onSelect: (key: EditSectionKey) => void
  className?: string
  /** Mobile: render as overlay when true */
  isOverlay?: boolean
  onCloseOverlay?: () => void
}

export function EditSidebar({ onSelect, className, isOverlay, onCloseOverlay }: EditSidebarProps) {
  const content = (
    <aside
      className={cn(
        'flex flex-col gap-1 rounded-lg border border-border bg-muted/30 p-2',
        'w-full min-w-[200px]',
        className
      )}
    >
      <p className="mb-1 px-2 py-1 text-xs font-medium text-muted-foreground">Editar dados</p>
      {SECTIONS.map(({ key, label, icon: Icon }) => (
        <Button
          key={key}
          variant="ghost"
          size="sm"
          className="justify-start gap-3 text-left"
          onClick={() => {
            onSelect(key)
            onCloseOverlay?.()
          }}
        >
          <Icon className="shrink-0 text-muted-foreground" />
          <span>{label}</span>
        </Button>
      ))}
    </aside>
  )

  if (isOverlay) {
    return (
      <>
        <div
          className="fixed inset-0 z-40 bg-black/50 md:hidden"
          onClick={onCloseOverlay}
          aria-hidden
        />
        <div className="fixed left-0 top-0 z-50 h-full w-[240px] border-r border-border bg-background p-4 pt-20 shadow-xl md:hidden">
          {content}
        </div>
      </>
    )
  }

  return content
}
